.form_new_product {
    background-color: rgb(231, 231, 231);
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 15px;
    width: 80%;
    min-width: 800px;
    margin: auto;
}

.form_new_product label {
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    width: 80%;
    display: flex;
    justify-content: space-around;
    /* background-color: aquamarine; */
}

input, select, textarea {
    width: 40%;
    height: auto;
    padding: 0;
    margin: 0;
    /* border: 1px solid black; */
}


label span {
    width: 30%;
}

.check_green {
    color: green;
    /* background-color: rgb(207, 126, 126); */
    width: 10%;
    /* margin: auto; */
    display: flex;
    justify-content: center;
    border: 2px rgb(231, 231, 231) solid;
}

.check_red {
    color: red;
    /* background-color: rgb(207, 126, 126); */
    width: 10%;
    /* margin: auto; */
    display: flex;
    justify-content: center;
    border: 2px red solid;
}
.check_facultatif {
    color: black;
    /* background-color: rgb(207, 126, 126); */
    width: 10%;
    /* margin: auto; */
    display: flex;
    justify-content: center;
    border: 2px rgb(231, 231, 231) solid;
}



.inputFile {
    /* background-color: rgb(139, 175, 206); */
    width: auto;
    margin: auto;
    margin-top: 0px;

}

.test {
    /* background-color: aqua; */
}




.productCardContainer {
    /* background-color: olive; */
    display: flex;
    flex-direction: column;
    /* aspect-ratio: 3/2.4; */
    padding: 5px;
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.productCardContainer>.top_info {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 5px auto;
}

.productCardContainer>.top_info>p {
    padding: 0;
    margin: 0;
}



.productCardContainer_picture_wrap {
    /* background-color: red; */
    aspect-ratio: 3/1.8;
}

.productCardContainer_picture {
    border-radius: 10px;
    /* max-width: 100%; */
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.productCardContainer_txt_container {
    /* background-color: aquamarine; */
    aspect-ratio: 3/0.6;
    padding: 10px;

    /* background-color: transparent; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 50% 50%;
    grid-gap: 2px;

    grid-template-areas:
        "name price"
        "colors button";
}

.productCardContainer-txt-container div {
    /* background-color: green; */
}

.pCC_txt_name {
    grid-area: name;
}

.pCC_txt_price {
    grid-area: price;
    justify-self: end;
}

.pCC_txt_colors {
    grid-area: colors;
}

.pCC_txt_button {
    grid-area: button;
    justify-self: end;
}

.pCC_txt_button button {
    /* color: white; */
    background-color: white;
    border: none;
    /* border: black 1px solid; */
    border-radius: 5px;
}


.modify_gallery {
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
}




.btn_option {
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-around;
    /* background-color: aqua; */
}

.btn_option button {
    border: 1px solid black;
    border: 2px solid white;
    /* border: none; */
    border-radius: 5px;
    padding: 15px;
    font-size: 1.2em;
    cursor: pointer;
}


.btn_option button:hover {
    border: 2px solid black;
    color: white;
    font-size: 1.2em;
}

.btn_option .supprimer {
    background-color: rgb(240, 72, 72);
}

.btn_option .modifier {
    background-color: rgb(33, 209, 57);
}

.productCardContainer {
    border: 1px solid black;
    border-radius: 9px;

}

.productCardContainer:hover {
    border: 2px solid black;
}


.test_ok {
    margin: 10px auto;
    height: 10px;
    width: 200px;
    background-color: green;
}

.test_false {
    margin: 10px auto;
    height: 10px;
    width: 200px;
    background-color: red;
}


