.Page2_main {
    margin-top: 5px;
    width: 95%;
    margin: auto;
    background-color: rgb(236, 236, 236);
    /* background-color: rgb(230, 230, 230); */
    height: calc(100vh - 120px);
    border-radius: 15px;
    border: 2px solid rgb(143, 143, 143);
    overflow: hidden;
}

.Page2_Header {
    /* background-color: aqua; */
    display: flex;
    flex-direction: row;
    background-color: rgb(219, 219, 219);

}

.Page2_Header_titre_active {
    /* background-color: red; */
    width: 50%;
    text-align: center;
    font-size: 2em;
    font-weight: 600;
    margin: 0;
    padding: 0;
    cursor: pointer;
    /* border-bottom: 2px solid rgb(143, 143, 143); */
    border-right: 2px solid rgb(143, 143, 143);
    /* border-top-right-radius: 15px; */

    
    background-color: rgb(236, 236, 236);
}
.Page2_Header_titre_unactive {
    /* background-color: red; */
    background-color: rgb(219, 219, 219);
    width: 50%;
    text-align: center;
    font-size: 2em;
    font-weight: 600;
    margin: 0;
    padding: 0;
    cursor: pointer;
    border-bottom: 2px solid rgb(143, 143, 143);
    border-right: 2px solid rgb(143, 143, 143);
    /* border-top-right-radius: 15px; */
}

.Page2_Header_titre_unactive:hover {
    /* background-color: red; */
    background-color: rgb(230, 230, 230);
    width: 50%;
    text-align: center;
    font-size: 2em;
    margin: 0;
    padding: 0;
    cursor: pointer;
    /* border-bottom: 2px solid rgb(143, 143, 143); */
    /* border-right: 2px solid rgb(143, 143, 143); */
    /* border-top-right-radius: 15px; */
}