.table_wrap {
    margin: 30px auto;
    width: 90%;
    height: 80%;
    overflow: scroll;
    background-color: aqua;
    background-color: rgba(155, 152, 190, 0.8);
}

.table_modifier {
    /* margin: 30px auto; */
    width: 100%;
}

.table_modifier th {
    padding: 10px;
    /* font-size: 0.6em; */
}

.table_ligne1 {
    background-color: rgb(185, 185, 185);
}

.table_ligne2 {
    background-color: rgb(155, 155, 155);
}

.table_ligne1:hover {
    background-color: rgba(155, 152, 190);
}

.table_ligne2:hover {
    background-color: rgba(155, 152, 190);
}

.icc_pen {
    color: orange;
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
    /* background-color: rgba(209, 209, 209, 0.219); */
}

.icc_pen:hover {
    color: rgb(211, 137, 0);
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
}

.icc_trash {
    color: red;
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
}

.icc_trash:hover {
    color: rgb(214, 0, 0);
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
}

.icc_check {
    color: rgb(0, 167, 0);
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
}

.icc_check:hover {
    color: rgb(0, 107, 0);
    cursor: pointer;
    font-size: 2em;
    padding: 5px;
}

.input_disabled {
    background-color: inherit;
    text-decoration: none;
    border: none;
    appearance: none;
    width: 100%;
}
.input_ok {
    /* background-color: inherit; */
    /* text-decoration: none; */
    /* border: none; */
    /* appearance: none; */
    width: 100%;
    padding: 0;
    margin: 0;
}




















